<template>
    <view class="things_user_interests">
        <!-- tabbar -->
        <cu-custom :refresh="preRrefresh"><block slot="content">个人权益</block></cu-custom>
        <!-- 导航栏 -->
        <scroll-view scroll-x class="bg-white nav">
            <view class="flex text-center navBox">
                <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                    {{item.name}}
                </view>
            </view>
        </scroll-view>
        <!-- 我的下级列表 -->
        <scroll-view v-if="tabCur == 1" class="things_width_full subordinate_list" scroll-y="true">
            <view class="flex justify-between align-center things_width_full subordinate_item">
                <!-- 头像 -->
                <view class="imageBox">
                    <image class="subordinate_item_cover" src="https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg" />
                </view>
                <view class="flex justify-between align-center item_right">
                    <view class="subordinate_item_name">用户名</view>
                    <view class="subordinate_item_profit">+1245</view>
                </view>
            </view>
            <!-- 空数据 -->
            <view class="kong_tip">暂无更多</view>
        </scroll-view>
        <!-- 我的推广海报 -->
        <scroll-view v-if="tabCur == 2" class="things_width_full poster_illustration" scroll-y="true">
            <view class="poster_box">
                <image class="poster_bg" src="./../../../../static/images/user/user_top_bg.png" mode="widthFix" />
                <view class="flex justify-between align-center user_info">
                    <view class="flex justify-start align-center usre_info_left">
                        <image class="user_cover" src="https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg" />
                        <view class="user_info_left_right">
                            <view class="user_name">用户名</view>
                            <view class="user_tip">邀请你加入</view>
                        </view>
                    </view>
                    <image class="code_image" src="https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg" />
                </view>
            </view>
            <view class="flex justify-center align-center save_btn_box">
                <button class="cu-btn save_btn">保存图片</button>
            </view>
        </scroll-view>
    </view>
</template>
<script>
let that = null;
export default {
    data() {
        return {
            tabCur: 1,
            scrollLeft: 0,
            tabData: [
                {
                    id: 1,
                    name: '我的客户'
                },
                {
                    id: 2,
                    name: '代言海报'
                }
            ]
        }
    },
    methods: {
        tabSelect(e) {
            this.tabCur = e.currentTarget.dataset.id;
            this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        that.tabCur = options.type;
    }
}
</script>
<style>
    page {
        width: 100vw;
        height: 100vh;
        background-color: #FFF;
    }
</style>
<style lang="scss" scoped>
    @import './index';
</style>
